"use client";

import { useForm } from "react-hook-form";
import { useRouter } from "next/navigation";
import useStore from "@/context/store";
import formStore from "@/context/form-store";
import { PageLoader } from "@/components/loader";
import MyCartLeft from "@/components/cart-step/MyCartLeft";
import OrderCart from "@/components/order-cart/OrderCart";
import { OrderRequest, CartItem } from "@/types";

export default function CartPage() {
  // — your stores & form
  const { cart, setQuantity, deleteCart, resetCart } = useStore();
  const { addCartForm, cartForm } = formStore();

  const {
    handleSubmit,
    control,
    setValue,
    watch,
    formState: { errors },
  } = useForm<OrderRequest>({
    defaultValues: { city: cartForm.city || "" },
  });

  const router = useRouter();

  // — keep onSubmit here
  const onSubmit = (data: OrderRequest) => {
    addCartForm(data);
    router.push("/cart/contacts");
  };

  // — pull out all the effect & selection logic
  const {
    isClient,
    selectedItems,
    toggleAllItems,
    toggleSingleItem,
    isAllChecked,
    selectedCards,
  } = useCartPage(cart as CartItem[]);

  // — loading until client
  if (!isClient) {
    return <PageLoader />;
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <div className="grid grid-cols-4 gap-[23px]">
        <div className="col-span-3">
          <MyCartLeft
            control={control}
            errors={errors}
            setValue={setValue}
            watch={watch}
            cart={cart}
            selectedItems={selectedItems}
            toggleAllItems={toggleAllItems}
            toggleSingleItem={toggleSingleItem}
            isAllChecked={isAllChecked}
            setQuantity={setQuantity}
            deleteCart={deleteCart}
            resetCart={resetCart}
          />
        </div>
        <div className="col-span-1">
          <OrderCart selectedCards={selectedCards} />
        </div>
      </div>
    </form>
  );
}
